@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600;700&display=swap);
html{font-size:100%}body{margin:0;font-family:'Open Sans', sans-serif}a:link,a:visited{text-decoration:none}ul{padding:0}nav ul li a,#home .content{color:white;-webkit-filter:drop-shadow(0 0 0.1rem black);filter:drop-shadow(0 0 0.1rem black)}.justify{text-align:justify}.white{color:white}nav{position:fixed;width:100%;opacity:0;font-weight:700;transition:opacity 0.2s, background-color 0.2s;z-index:1}nav.transparent{opacity:1}nav.opaque{opacity:1;background-color:#161b22}nav.opaque ul li a{color:#f0f6fc}nav ul{list-style:none;margin:0;text-align:center}nav ul li{display:inline-block;padding:1rem 0}nav ul li a{padding:1rem 1rem;transition:color 0.2s}nav ul li a.active{color:orange}nav ul li a:hover{cursor:pointer}section{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;text-align:center}section .content{margin-left:2rem;margin-right:2rem;max-width:50rem}section .section-name{-webkit-margin-before:0;margin-block-start:0;padding-top:7.5rem;color:orange}#home{height:100vh;min-height:55vw}#home #rain-image,#home #rain-video{position:absolute;object-fit:cover;object-position:50% 0%;width:100%;height:100vh;min-height:inherit}#home #rain-image{z-index:-2}#home #rain-video{position:fixed;z-index:-1}#home>div{-webkit-flex:1 1;flex:1 1}#home .content{padding:5rem 0;font-weight:700}#home .content #name,#home .content #roles{margin:0}#home .content #name{font-size:3rem}#home .content #roles{margin-bottom:0.25rem;font-size:0.9rem}#home .content #socials{font-size:3rem}#home .content #socials *{margin:0 0.5rem}#home .content #wip{font-size:1.25rem}#home .content #wip *{margin:0 0.25rem}#about,#resume{min-height:100vh;font-weight:600}#about{background-color:#0d1117;color:#8b949e}#about *{max-width:50rem}#about a{color:white}#about #about-introduction *{max-width:50rem;margin:5rem inherit}#about #about-introduction * span{color:#a7aeb5}#about #about-info{max-width:50rem;margin-bottom:5rem}#about #about-info div{text-align:left}#resume{background-color:white}#resume a{color:orange}@media (min-width: 60vh){#home #rain-image,#home #rain-video{object-position:0% 100%}#about #about-info{display:-webkit-flex;display:flex;grid-gap:2.5rem;gap:2.5rem}#about #about-info div{-webkit-flex:1 1;flex:1 1}}

